.Container {
    margin-top: 3rem;
    background-color: rgb(211, 209, 209);
    padding: 0.7rem;
    padding-bottom: 2rem;
    padding-top: 1rem;
    border-radius: 0.5rem;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.144);
}

.Container label {
    display: block;
    margin-bottom: 0.7rem;
    margin-left: 1rem;
    font-size: 1.1rem;
    font-family: "Noto Sans", sans-serif;
    color: rgb(36, 36, 32);
}

.Container input {
    height: 1.7rem;
    width: 20rem;
    display: block;
    margin-bottom: 0.2rem;
    border: 0;
    padding-left: 0.3rem;
    font-family: "Noto Sans", sans-serif;
    font-size: 0.9rem;
}

.Container input:focus {
    outline: 1px solid black;
}

.Container div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.Container button {
    padding: 0.3rem 1.2rem;
    font-size: 0.9rem;
    border-radius: 0.2rem;
    border: 1px solid black;
    background-color: rgb(224, 255, 224);
    cursor: pointer;
    font-weight: 400;
    font-family: "Noto Sans", sans-serif;
    
}

@media (max-width: 360px) {
    .Container input {
        height: 1.7rem;
        width: 17rem;
        display: block;
        margin-bottom: 0.2rem;
        border: 0;
        padding-left: 0.3rem;
        font-family: "Noto Sans", sans-serif;
        font-size: 0.9rem;
    }
}