.Container {
    display: flex; 
    gap: 2rem;
    background-color: white;
    padding: 0.7rem;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.144);
    justify-content: center;
    align-items: center;

}

.Container p {
    font-size: 0.9rem;
    font-family: "Roboto Mono", monospace;
    font-weight: 400;
}

.Check {
    font-size: 1.3rem;
    cursor: pointer;
    color: rgb(153, 255, 0);
}

.Bin {
    font-size: 1.1rem;
    cursor: pointer;
    color: rgb(107, 110, 90);
}

.destacado {
    text-decoration: line-through;
}

.apagar {
    display: none;
}

@media (max-width: 360px) {
    .Container {
        gap: 0.7rem;
        background-color: white;
        padding: 0.7rem;
        box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.144);
        justify-content: center;
        align-items: center;
    
    }
}